import React from 'react';

import ImageWebp from "../../layout/ImageWebp/ImageWebp";

import styles from "./TechnologiesBlock.module.scss"



function TechnologiesBlock({iconImagesArr}) {

    const iconImages = iconImagesArr.map(({img, webp}, index) => (
        <ImageWebp
            key={index}
            srcSet={webp}
            src={img}
            alt="Technologies Icon"
            className={styles["technologiesBlock__iconImg"]}
            pictureClass={styles["technologiesBlock__iconPicture"]}

        />
    ))

    return (
        <section className={`${styles['technologiesBlock']} container`}>
            <h3 className={`${styles["technologiesBlock__title"]} titleDef`}>OUR<span
                className="lightBlueText"> TECHNOLOGIES</span>
            </h3>
            <p className={`${styles["technologiesBlock__contentText"]} contentTextDef`}>We work with a variety of modern development tools and choose the ones that are best-fitting for each particular case.</p>
            <div className={styles["technologiesBlock__iconsBlock"]}>
                <div
                    className={`${styles["technologiesBlock__marque"]} ${styles["technologiesBlock__marque_1"]}`}>
                    {iconImages}
                </div>
                <div
                    className={`${styles["technologiesBlock__marque"]} ${styles["technologiesBlock__marque_2"]}`}>
                    {iconImages}
                </div>
            </div>
        </section>
    );
}

export default TechnologiesBlock;